import React from 'react'
import PropTypes from 'prop-types'
import { Router } from 'react-router'
import Layout from './components/Layout'
import fetch from './utils/request'
import { MESSAGE, TOKEN } from './reducers/websocket'
import wsClient from './utils/wsClient'

const client = new wsClient('ws://localhost:3000', 'intoyun-demo-websocket')

function routerConfig ({ history, app }) {
  const routes = [{
    path: '/',
    name: 'laptop',
    breadcrumbName: '首页',
    component: Layout,
    onEnter (nextState, replace) {
      try {
        const { token } = app._store.getState().websocket
        if (!token) {
          fetch('/token').then(token => app._store.dispatch({ type: TOKEN, token }))
        }

        // 连接Websocket
        client.onMessage = (payload) => {
          try {
            let { collection } = app._store.getState().websocket
            const jsonParse = JSON.parse(payload)
            if (collection.length < 1 || !collection.find(model => model.devId === jsonParse.devId)) {
              collection = collection.concat(jsonParse)
            }

            app._store.dispatch({ type: MESSAGE, realtime: jsonParse, collection })
          } catch (err) {
            console.error(err)
          }
        }
        client.connect()
      } catch (error) {
        console.log(error)
      }
    },
    indexRoute: {
      onEnter: (nextState, replace) => replace('/gateway')
    },
    childRoutes: (r => r.keys().map(key => r(key).default(app))
    )(require.context('./', true, /^\.\/routes\/((?!\/)[\s\S])+\/route\.js$/))
  }]

  return <Router history={history} routes={routes} />
}

routerConfig.propTypes = {
  history: PropTypes.object,
  app: PropTypes.object
}

export default routerConfig



