import fetch from '../utils/request'

export default {
  namespace: 'device',
  state: { list: [] },
  reducers: {
    fetchAll: (state, { payload }) => ({
      ...state,
      list: payload
    })
  }
}

export function fetchAll () {
  return async (dispatch, getState) => {
    try {
      const respone = await fetch('/api/device')
      dispatch({ type: 'device/fetchAll', payload: respone })
      return respone
    } catch (error) {
      console.error(error)
    }

    return false
  }
}



